import React from "react";
import styled from "styled-components";

const UserInfoManagement = ({ className }) => {
  return (
    <section className={`${className} user__management__info col-12 col-lg-5`}>
      <header className="fs__title fw-bolder">Thông tin cá nhân</header>
      <div className="avatar mt-4">
        <img src="/images/meow.jpg" alt="avatar" />
      </div>
      <div className="form mt-4">
        <label for="" className="fs__text fw-bolder">
          Name
          <input type="text" name="" id="" placeholder="Mavis" />
        </label>
        <label for="" className="fs__text fw-bolder">
          Name
          <input type="text" name="" id="" placeholder="Mavis" />
        </label>
        <label for="" className="fs__text fw-bolder">
          Name
          <input type="text" name="" id="" placeholder="Mavis" />
        </label>

        <input type="submit" value="Sửa thông tin" />
      </div>
    </section>
  );
};

export default styled(UserInfoManagement)`
  &.user__management {
    &__info {
      /* width: 50%; */
      .avatar {
        width: 100%;
        img {
          /* $size: 100px; */
          width: 100px;
          height: 100px;
          border-radius: 50%;
        }
      }
      .form {
        label {
          display: flex;
          flex-direction: column;
        }
        input:not([type="submit"]) {
          margin: 10px 0;
          background-color: rgba(255, 255, 255, 0);
          border: 1px solid ${(props) => props.theme.colorSecondary};
          padding: 10px 10px 10px 0;
          text-indent: 10px;
          border-radius: 8px;
          outline: none;
          color: #fafafa;
          &::placeholder {
            color: #fafafa;
          }
          &:focus {
            border-width: 2px;
          }
        }
        input[type="submit"] {
          padding: 10px;
          background-color: ${(props) => props.theme.colorSecondary};
          border: 0;
          border-radius: 8px;
          font-weight: bold;
          font-size: 16px;
          margin: 10px 0;
        }
      }
    }
  }
`;
